
import Swiper from 'vux/src/components/swiper/swiper.vue'

import html2canvas from "html2canvas"; import haiBao1 from "../../../../assets/haiBao1.png";
import haiBao2 from "../../../../assets/haiBao2.png";
import haiBao3 from "../../../../assets/haiBao3.png";
const imgList = [haiBao1,haiBao2,haiBao3];
const demoList = imgList.map((one, index) => ({
url: 'javascript:',
img: one
}))
export default {
data() {
return {
list: demoList,
value: "",
p_uid:"",
activity_id:""
};
},
components: {
html2canvas,Swiper
},
mounted(){
if(this.$route.params.activity_id){this.activity_id=this.$route.params.activity_id}
this.value='http://192.168.0.125:3001/#/redPacket?p_uid='+this.$store.state.user.uid+"&activity_id="+this.activity_id;

},
methods: {

toSave() {
html2canvas(document.getElementById("imageWrapper")).then(canvas => {
let saveUrl = canvas.toDataURL("image/jpg");
let aLink = document.createElement("a");
let blob = this.base64ToBlob(saveUrl);
let evt = document.createEvent("HTMLEvents");
evt.initEvent("click", true, true);
aLink.download = "haiBao.jpg";
aLink.href = URL.createObjectURL(blob);
aLink.click();
console.log("点击保存:"+aLink);
});
}, //这里把图片转base64
base64ToBlob(code) {
let parts = code.split(";base64,");
let contentType = parts[0].split(":")[1];
let raw = window.atob(parts[1]);
let rawLength = raw.length;
let uInt8Array = new Uint8Array(rawLength);
for (let i = 0; i < rawLength; ++i) {
uInt8Array[i] = raw.charCodeAt(i);
}
return new Blob([uInt8Array], { type: contentType });
}
}
};



